import _tool from '@/utils/tools.js';
export default {
	name: 'hot-topic-list',
	data() {
		return {
			list_num: 1,
		}
	},
	props: {
		list_data: {
			type: Array,
			default: () => {
				return []
			}
		},
		isSort: {
			type: String,
			default: "scroll"
		},
	},
	watch: {
		list_data() {
			this.getScrollPageNum(this.list_data);
		}
	},
	created() {
		this.getScrollPageNum(this.list_data);
	},
	methods: {
		listTap(data,index) {
			this.$emit('listTap', {
				data,
				index
			});
		},
		viewAllTap() {
			this.$emit('viewAllTap');
		},
		getScrollPageNum(data) {
			this.list_num =  _tool.getPageNum(data.length,3);
		},
		getItmeList(index,indexs) {
			let max = (index + 1 ) * 3;
			let min = max - 3;
			if(indexs >= min && indexs < max) {
				return true;
			} else {
				return false;
			}
		}
	}
}
